<template>
  <article class="modal shopping-cart">
    <div class="modal__wrapper">
      <div class="modal__container">
        <form class="modal__content" acrtion="">
          <div class="modal__col">
            <div class="modal__named">
              <h1 class="modal__title">
                заказ
              </h1>
              <h3 class="modal__model">
                Ваш
              </h3>
            </div>
            <div class="modal__description">
              <label for="name">Ваше имя</label> <span><input type="text" id="name" placeholder="имя" required> <input type="text" id="last_name" placeholder="фамилия" required></span>
              <label for="phone">Телефон</label> <span><input type="phone" id="phone" placeholder="+38 (0**) *******" required></span>
              <label for="mail">E-mail</label> <span><input type="mail" id="mail" placeholder="example@yourmail.com" required></span>
              <label for="pay">Способ оплаты</label>
              <span>
                <select id="pay" @change="showSubInfo" v-model="pay">
                   <option disabled>Выберите способ оплаты</option>
                   <option value="nova_poshta">Наложенный платеж "Нова Пошта" (при получении)</option>
                   <option value="zabery_sam" selected>Заберу сам</option>
                 </select>
              </span>
            </div>
            <div class="modal__btn">
                <button>
                  подтвердить
                </button>
            </div>
          </div>
          <div class="modal__col">
            <div class="modal__description modal__input_cases">
              <p>Выбранные позиции</p>
            </div>
            <ul>
              <li v-for="index in shoppingList">
                {{ index.id }} - {{ index.type }} {{ index.modelCase }} - {{ index.modelPhone }} - {{ index.price }}
              </li>
            </ul>
          </div>
        </form>
      </div>
      <div class="modal__footer">
        <div class="modal__back">
          <router-link to="/"  tag="a" :exact="true" alt="title">
            <img src="img/goBack.png" alt="">
          </router-link>
        </div>
      </div>
    </div>
  </article>
</template>

<script>
  import store from '../../store.js';

  export default {
    name: "ShoppingCartList",
    data() {
      return {
        pay: 'Выберите способ оплаты',
        mobile_case: [
          {
            id: 'dark_black',
            img: 'img/case/black.png',
            type: 'apple iphone',
            model: 'dark black',
            size_price: [
              {
                model: 'XS',
                size: '6.5',
                price: '325'
              },
              {
                model: 'XR',
                size: '6.1',
                price: '275'
              },
              {
                model: 'X',
                size: '5.8',
                price: '250'
              },
              {
                model: '8',
                size: '5.5',
                price: '225'
              },
              {
                model: '7 plus',
                size: '6.3',
                price: '175'
              },
              {
                model: '7',
                size: '4.8',
                price: '150'
              }
            ]
          },
          {
            id: 'deep_brown',
            img: 'img/case/yellow.png',
            type: 'apple iphone',
            model: 'bright yellow',
            size_price: [
              {
                model: 'Xr',
                size: '6.5',
                price: '325'
              },
              {
                model: 'Xs',
                size: '6.1',
                price: '275'
              },
              {
                model: 'X',
                size: '5.8',
                price: '250'
              }
            ]
          },
          {
            id: 'brown',
            img: 'img/case/brown.png',
            type: 'apple iphone',
            model: 'deep brown',
            size_price: [
              {
                model: 'X',
                size: '6.5',
                price: '325'
              },
              {
                model: 'Xs',
                size: '6.1',
                price: '275'
              },
              {
                model: 'Xr',
                size: '5.8',
                price: '250'
              }
            ]
          },
          {
            id: 'dark',
            img: 'img/case/black.png',
            type: 'apple iphone',
            model: 'dark black',
            size_price: [
              {
                model: 'Xs',
                size: '6.5',
                price: '325'
              },
              {
                model: 'XR',
                size: '6.1',
                price: '275'
              },
              {
                model: 'X',
                size: '5.8',
                price: '250'
              }
            ]
          },
          {
            id: 'deep',
            img: 'img/case/yellow.png',
            type: 'apple iphone',
            model: 'deep brown',
            size_price: [
              {
                model: 'XS',
                size: '6.5',
                price: '325'
              },
              {
                model: 'XR',
                size: '6.1',
                price: '275'
              },
              {
                model: 'X',
                size: '5.8',
                price: '250'
              }
            ]
          },
        ],
        shoppingList: [],
        storageList: null,

    }
    },
    created() {
      let vm = this;
      vm.storageList = store.getStore();

      // console.log( store.getStore() )
      for(let item in vm.mobile_case ) {
        for( let key in vm.storageList ) {
          // console.log( item.size_price )
          for(let index in vm.mobile_case[item].size_price) {
            if( vm.mobile_case[item].id == vm.storageList[key].id &&
                vm.mobile_case[item].size_price[index].model == vm.storageList[key].model ) {
              let arr = {
                id: vm.mobile_case[item].id,
                img: vm.mobile_case[item].img,
                type: vm.mobile_case[item].type,
                modelCase: vm.mobile_case[item].model,
                modelPhone: vm.storageList[key].model,
                size: vm.mobile_case[item].size_price[index].size,
                price: vm.mobile_case[item].size_price[index].price
              };
              vm.shoppingList.push( arr )
            }
          }
        }
      };



      console.log( vm.shoppingList );
    },
    methods: {
      showSubInfo(e) {
        console.log( this.pay )
      }
    }
  }
</script>



<style lang="scss">
  .shopping-cart.modal {
    .modal {
      &__title {

      }
      &__model {
        color: #ff0000;
        top: -2.75rem;
        left: -40%;
      }
      &__col {
        &:first-child {
          align-items: center;
          /*width: 60%;*/
        }
        &:last-child {
          /*width: 40%;*/
        }
      }
      &__btn {
        button {
          background-color: #e40000;
          padding: 1.5rem 2.75rem;

        }
      }
      &__description {
        /*width: 90%;*/
        font-family: Oswald;
        /*font-family: "Oswald - Demi";*/
        font-size: 2rem;
        font-weight: 500;
        text-transform: none;
        /*line-height: 60px;*/
        /*&:first-child {*/
        display: grid;
        grid-template-columns: 1fr 2fr;
        /*}*/
        > * {
          display: flex;
          justify-content: space-between;
          align-items: center;
        }
        span {
          margin: .75rem;
          input,
          select {
            width: 100%;
            color: rgba(0,0,0, .7);
            font-family: "Oswald - Regular Italic";
            font-size: 18px;
            font-weight: 400;
            padding:  .5rem 1rem;
            border: 2px solid #000;
            border-radius: 20px;
            background: transparent;
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
            &#name {
              width: 30%;
            }
            &#last_name {
              width: 45%;
            }
          }
        }
      }
    }
  }

  ::-webkit-input-placeholder {
    color: #bcbcbc;
    font-family: "Oswald - Regular Italic";
    font-size: 18px;
    font-weight: 400;
  }
  ::-moz-placeholder {
    color: #bcbcbc;
    font-family: "Oswald - Regular Italic";
    font-size: 18px;
    font-weight: 400;
  }
  :-ms-input-placeholder {
    color: #bcbcbc;
    font-family: "Oswald - Regular Italic";
    font-size: 18px;
    font-weight: 400;
  }
</style>



